<template>
    <div>
      <template>
        <el-carousel indicator-position="outside" height="600px"
        autoplay interval="1000" :loop="true" arrow="allways"
        >
          <el-carousel-item v-for="item in imgs" :key="item">
            <el-image
              style="height: 100%;width: 100%;"
              :src="item"
              ></el-image>
            <!--:fit="contain"-->
          </el-carousel-item>
        </el-carousel>
      </template>
    </div>
</template>

<script>
  import homeImg1 from "../assets/indexImgs/869605.jpg";
  import homeImg2 from "../assets/indexImgs/320746.jpg";
  import homeImg3 from "../assets/indexImgs/659052.jpg";
  import homeImg4 from "../assets/indexImgs/889639.jpg";
  import homeImg5 from "../assets/indexImgs/642648.jpg";
  import homeImg6 from "../assets/indexImgs/1069438.jpg";
    export default {
        name: "Index",
      data(){
          return{
            imgs:[homeImg1,homeImg2,homeImg3,homeImg4,homeImg5,homeImg6]
          }
      }
    }
</script>

<style scoped>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>



