<template>
    <div>
      <el-table :height="400"
        :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column
          label="编号"
          width="180">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="姓名"
          width="180">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>姓名: {{ scope.row.name }}</p>
              <p>住址: {{ scope.row.address }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.name }}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="生日" prop="bir"></el-table-column>
        <el-table-column label="性别" prop="sex"></el-table-column>
        <el-table-column label="地址" prop="address"></el-table-column>
        <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder="输入关键字搜索"/>
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">Edit</el-button>

            <el-popconfirm
              confirmButtonText='好的'
              cancelButtonText='不用了'
              icon="el-icon-info"
              iconColor="red"
              title="确定删除吗？"
              @onConfirm="handleDelete(scope.$index, scope.row)"
            >
              <el-button size="mini"
                         type="danger"
                         slot="reference">删除</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <!--分页-->
      <el-row>
        <el-col :span="10" :offset="10">
          <el-pagination background
            layout="prev, pager, next,jumper,total,sizes"
            prev-text="上一页"
            next-text="下一页"
            :page-size="size"
            :current-page="pageNow"
            :page-sizes="[2,4,6,8,10]"
            :total="total"
            @current-change="findPage"
            @size-change="findSize"
          >
          </el-pagination>
        </el-col>
      </el-row>


      <el-button style="margin: 10px 0px;" @click="saveUserInfo" type="success" >添加</el-button>

      <transition name="el-zoom-in-center">
        <div v-show="show" class="transition-box">
          <el-form  :hide-required-asterisk="false" :rules="rules" ref="userForm" :model="form" label-width="80px" label-suffix=" :">

            <el-form-item label="姓名" prop="name">
              <el-input v-model="form.name"></el-input>
            </el-form-item>

            <el-form-item label="生日" prop="bir">
              <el-date-picker value-format="yyyy-MM-dd" placeholder="选择时间" v-model="form.bir" style="width: 100%;"></el-date-picker>
            </el-form-item>


            <el-form-item label="性别" prop="sex">
              <el-radio-group v-model="form.sex">
                <el-radio label="男"></el-radio>
                <el-radio label="女"></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item label="详细地址" prop="address">
              <el-input type="textarea" v-model="form.address"></el-input>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="onSubmit('userForm')">保存用户信息</el-button>
              <el-button @click="saveUserInfo">重置</el-button>
            </el-form-item>
          </el-form>
        </div>
        </transition>

    </div>
</template>

<script>
    export default {
        name: "List",
      data() {
        return {
          tableData: [],
          search:"",
          show:false,
          form:{
            name: '',
            bir:'',
            sex:'男',
            address:''
          },
          total:0
          ,
          pageNow:1,
          size:4,
          rules: {
            name: [
              {required: true, message: '请输入用户姓名', trigger: 'blur'}
            ],
            bir:[
              {required: true, message: '请输入用户的生日', trigger: 'blur'}
            ],
            address:[
              {required:true,message:"请输入用户的地址", trigger:"blur"}
            ]
          }
        }
      },
      methods: {
        findSize(size){
          console.log(size);
          this.size=size;
          this.findAllTableData(this.page,size);
        },
        findPage(page){//处理分页
          this.page=page;
          this.findAllTableData(page,this.size);
        },
          saveUserInfo(){
            this.show=true,
            this.form={sex:'男'};
          }
          ,
        handleEdit(index, row) {
          console.log(index, row);
          this.show=true;
          this.form=row;


        },
        handleDelete(index, row) {
          console.log(index, row);
          //发送axios请求处理删除
          this.$http.get("http://localhost:8989/user/delete?id="+row.id).then(res=>{
            if (res.data.status){
              this.$message({
                message:res.data.msg,
                type:"success"
              });
              this.findAllTableData();
            } else {
              this.$message.error(res.data.msg);
            }
          })
        },
        onSubmit(userForm) {
            this.$refs[userForm].validate((valid) => {
              if (valid) {
                //发送一个请求
                this.$http.post("http://localhost:8989/user/saveOrUpdate",this.form).then(res=>{
                  console.log(res.data);
                  if (res.data.status){
                    this.$message({
                      message:"恭喜你, "+res.data.msg,
                      type:"success"
                    });
                    //清空表单信息
                    this.form={
                      sex:'男'
                    };
                    //隐藏表单
                    this.show=false;
                    //调用刷新数据的方法
                    this.findAllTableData();
                  }else {
                    this.$message.error(res.data.msg);
                  }
                });
              } else {
                this.$message.error("输入的数据不合法");
                return false;
              }
            });
        },
        findAllTableData(page,size) {
          //传过来的page size 是有值的话 使用page size
          //如果没有值得话 使用默认定义的page size 值
          page=page?page:this.pageNow;
          size=size?size:this.size;
          this.$http.get("http://localhost:8989/user/findByPage?pageNow="+page+"&pageSize="+size).then(res => {
            this.tableData = res.data.users;
            this.total=res.data.total;
            console.log(res);
          });
        }
      },
      created(){
          this.findAllTableData();

          }
      }

</script>

<style scoped >
  .transition-box {
    margin-bottom: 10px;
    width: 100%;
    height: 400px;
    border-radius: 4px;
    /*background-color: #409EFF;*/
    /*text-align: center;*/
    /*color: #fff;*/
    padding: 40px 20px;
    box-sizing: border-box;
    margin-right: 20px;
  }

</style>



